$primary-color: #efe6d5;
$secondary-color: #9dbeb7;
$accent-color: #e73213;
$font-family: 'Lato', sans-serif;

body {
  background-color: $secondary-color;
  font-family: $font-family;
}

.row {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-left: -15px;
  margin-right: -15px;
}
